let SlidersArray = []
function sliderProducts() {
    $(".products__slider").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.products__slider[data-slider-id="${index}"] .swiper`, {
            slidesPerView: 1,
            spaceBetween: 15,
            navigation: {
                nextEl: `.products__slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.products__slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `.products__slider[data-slider-id="${index}"] .swiper-pagination`,
                clickable: true,
            },
            breakpoints: {
                320: {
                    slidesPerView: 1.3,
                    spaceBetween: 15,
                },
                401: {
                    slidesPerView: 1.5,
                    spaceBetween: 15,
                },
                577: {
                    slidesPerView: 2.1,
                    spaceBetween: 15,
                },
                769: {
                    slidesPerView: 2.5,
                    spaceBetween: 20,
                },
                993: {
                    slidesPerView: 3,
                    spaceBetween: 30,
                }
            }
        })
    })
}

